/**
 * Base
 */
* {
  box-sizing: border-box;
}
*:before,
*:after {
  box-sizing: border-box;
}
body {
  background-color: white;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: normal;
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: black;
  line-height: 1.5;
  overflow-x: hidden;
  position: relative;
  width: 100%;
}
@media (min-width: 1200px) {
  body {
    font-size: 18px;
  }
}
a {
  color: blue;
  text-decoration: underline;
}
a:hover,
a:focus {
  color: #0000cc;
}
figure {
  margin: 0;
  background-size: auto 100%;
  background-position: center center;
  background-repeat: no-repeat;
}
figure img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
  margin: 0 auto;
  transition: opacity 0.2s;
}
figure.contain-width img {
  width: auto;
}
/* Elements
********************/
/*
 * Element: Buttons
 */
.button {
  display: inline-block;
  text-align: center;
  vertical-align: middle;
  touch-action: manipulation;
  cursor: pointer;
  background-image: none;
  background-color: #336699;
  white-space: nowrap;
  user-select: none;
  position: relative;
  font-weight: 400;
  font-family: 'Roboto', 'Helvetica', 'Arial', 'sans-serif';
  font-size: 14px;
  transition: background 0.3s, color 0.3s, border-color 0.3s;
  padding: 10px 20px;
  color: #ffffff;
  border: 1px solid transparent;
  text-decoration: none;
}
.button:hover,
.button:focus {
  color: #ffffff;
  background: #264c73;
  text-decoration: none;
  outline: none;
}
.button[disabled],
[disabled] .button {
  cursor: not-allowed;
}
.button--ghost {
  color: #363636;
  border: solid 1px #336699;
  background-color: transparent;
}
.button--ghost:hover,
.button--ghost:focus {
  color: #ffffff;
  background-color: #336699;
}
.button--spacing-top {
  margin-top: 20px;
}
/*
* Element: Headers
*/
.heading {
  color: #336699;
  font-size: 22px;
  font-weight: 600;
  font-family: 'SkipLegDay', 'Helvetica', 'Arial', 'sans-serif';
  margin-bottom: 0;
  margin-top: 0;
}
.heading__column {
  color: #000000;
}
/*
* Element: Headers
*/
.heading-page {
  font-size: 24px;
  color: #336699;
  font-family: 'SkipLegDay', 'Helvetica', 'Arial', 'sans-serif';
}
/*
 * Element: Link
 */
.link {
  color: #336699;
  font-family: 'Roboto', 'Helvetica', 'Arial', 'sans-serif';
  font-weight: 600;
  font-size: 16px;
}
.link:hover,
.link:focus {
  color: #264c73;
  outline: none;
}
