/*
 * Element: Link Social
 */
.social-link {
  position: relative;
  width: 40px;
  height: 40px;
  background-color: #acacac;
  border-radius: 50%;
}
.social-link:hover,
.social-link:focus {
  background: #939393;
}
.social-link__icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 20px;
  height: 20px;
  fill: #ffffff;
}
.social-link--facebook {
  background-color: #3b5998;
}
.social-link--facebook:hover,
.social-link--facebook:focus {
  background: #2d4373;
}
.social-link--twitter {
  background-color: #00aced;
}
.social-link--twitter:hover,
.social-link--twitter:focus {
  background: #0087ba;
}
.social-link--google-plus {
  background-color: #d34836;
}
.social-link--google-plus:hover,
.social-link--google-plus:focus {
  background: #b03626;
}
/*
 * Element: logo
 */
.logo {
  width: 100px;
}
/*
* Element: Label
*/
.label {
  font-family: 'Roboto', 'Helvetica', 'Arial', 'sans-serif';
  color: #363636;
  display: inline-block;
  max-width: 100%;
  margin-bottom: 5px;
  font-weight: bold;
}
.label--block {
  display: block;
}
/*
* Element: Label
*/
.textarea {
  display: block;
  width: 100%;
  min-height: 100px;
  padding: 10px;
  font-size: 16px;
  line-height: 1;
  font-family: 'Roboto', 'Helvetica', 'Arial', 'sans-serif';
  color: #363636;
  background-color: #ffffff;
  background-image: none;
  border: 1px solid #d7d7d7;
  border-radius: 10px;
  resize: none;
}
.textarea:focus {
  outline: none;
}
/*
* Element: Input fields
*/
.input {
  display: block;
  width: 100%;
  height: 40px;
  padding: 0 20px;
  font-size: 16px;
  line-height: 1;
  font-family: 'Roboto', 'Helvetica', 'Arial', 'sans-serif';
  color: #363636;
  background-color: #ffffff;
  background-image: none;
  border: 1px solid #d7d7d7;
  border-radius: 10px;
  vertical-align: bottom;
}
.input:focus {
  outline: 0;
}
/* Modules
********************/
/*
 * Module: Box
 */
.box__inner {
  border: 1px solid #d7d7d7;
  border-radius: 4px;
  background-color: #ffffff;
  padding: 20px;
}
.box p + p {
  margin-top: 15px;
}
/*
 * Module: Box Image
 */
.box-image {
  position: relative;
}
.box-image__inner {
  border: 1px solid #d7d7d7;
  border-radius: 4px;
  background-image: url('https://unsplash.it/400/300');
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 300px;
  position: relative;
}
.box-image__content {
  background-color: rgba(255, 255, 255, 0.75);
  bottom: 0;
  left: 0;
  padding: 20px;
  position: absolute;
  right: 0;
}
.box-image p + p {
  margin-top: 15px;
}
.form-field + .form-field {
  margin-top: 20px;
}
.link-list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.link-list__item + .link-list__item {
  margin-top: 5px;
}
@media (max-width: 992px) {
  .nav-main {
    display: none;
  }
}
.nav-main-list {
  list-style: none;
  padding: 0;
  margin: 0;
}
.nav-main-list__item {
  display: inline-block;
  margin-right: 5px;
  padding: 15px;
  font-size: 17px;
}
@media (max-width: 1440px) {
  .nav-main-list__item {
    font-size: 15px;
  }
}
.nav-main-list__item:hover {
  background-color: red;
  color: white;
}
.nav-main-list__item:hover a {
  color: white;
  text-decoration: none;
}
.nav-main-list__item:last-child {
  margin-right: 0;
}
.nav-main-list__item.active {
  background-color: red;
  color: white;
}
.nav-main-list__item.active a {
  color: white;
}
.nav-main-list__item__link {
  color: gray;
  text-decoration: none;
}
/*
 * Modul: Social Media Collection
 */
.social-media__list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.social-media__item {
  float: left;
}
.social-media__item + .social-media__item {
  margin-left: 10px;
}
/* Components
********************/
/* Helpers
********************/
.grid {
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}
@media (min-width: 992px) {
  .grid {
    padding-left: 60px;
    padding-right: 60px;
  }
}
.grid__inner {
  display: flex;
  flex-wrap: wrap;
  margin-right: -15px;
  margin-left: -15px;
}
.grid--full-width {
  max-width: 2000px;
  padding-left: 0;
  padding-right: 0;
}
.grid--site-width {
  max-width: 1560px;
}
.grid--two-columns {
  max-width: 1500px;
}
.grid--two-columns > .grid-column {
  width: 100%;
}
@media (min-width: 992px) {
  .grid--two-columns > .grid-column {
    width: calc(50% - 30px);
  }
}
.grid--three-columns {
  max-width: 1500px;
}
.grid--three-columns > .grid-column {
  width: 100%;
}
@media (min-width: 992px) {
  .grid--three-columns > .grid-column {
    width: calc(33.33% - 30px);
  }
}
.grid--two-third {
  max-width: 1500px;
}
.grid--two-third > .grid-column {
  width: 100%;
}
@media (min-width: 992px) {
  .grid--two-third > .grid-column {
    width: calc(66.66% - 30px);
  }
}
.grid--two-third > .grid-column:last-child {
  width: 100%;
}
@media (min-width: 992px) {
  .grid--two-third > .grid-column:last-child {
    width: calc(33.33% - 30px);
  }
}
.grid-column {
  margin: 15px;
}
